const game = document.querySelector(".game__area");
const root = game.getContext("2d");

game.width = 600; //window.innerWidth
game.height = window.innerHeight - 10; //window.innerHeight

const gravity = 1.5;

class Bird{
  constructor() {
    this.position = {
      x: game.width / 2,
      y: game.height / 2,
    }
    this.size = {
      radius: 12,
    }
    this.velocity = {
      x: 0,
      y: 3
    }
    this.jump = {
      height: 0,
      length: 7,
      count: 0,
    }
   
  }


  draw(){
    root.beginPath();
    root.arc(this.position.x, this.position.y, this.size.radius, 0, Math.PI*2, false);
    root.fillStyle = 'yellow';
    root.fill();
    root.closePath();
  }

  update(){
    this.draw();
    this.position.y += Math.floor((this.velocity.y + gravity)) - this.jump.height;
    this.jumpBird();
    this.collisionWall();
  }

  collisionWall(){
    if(this.position.y + this.size.radius > game.height){
      this.position.y =  game.height/2; 
    } 
    if(this.position.y - this.size.radius < 0){
      this.position.y =  game.height/2; 
    } 
  }
  jumpBird(){
    if(keys.space.pressed){
      this.jump.count++;
      this.jump.height = 2*this.jump.length*Math.sin(Math.PI*this.jump.count/this.jump.length);
    }
    if(this.jump.count > this.jump.length) {
      this.jump.count = 0;
      this.jump.height = 0;
      keys.space.pressed = false;
    }
  }
};

class Pipe {
  constructor(x,y) {
    this.position = {
      x:x,
      y:y
    }
    this.size = {
      width: 60,
      height: 700,
    }
  }

  draw(){
    root.fillStyle = "green";
    root.fillRect(this.position.x,this.position.y,this.size.width,this.size.height);
  }
  update(){
    this.draw();
    this.replace();
    this.position.x -= 3;
  }
  replace(){
    if(this.position.x + this.size.width < 0){
      this.position.x = 1000;
    }
  }
};



let bird = new Bird();//create a bird
//create pipes
let keys = {
  space: {
    pressed: false,
  },
}; //pressedSpaceOrNot


function createPairOfPipes() {
  let pipes = [];
  let randomY = Math.floor(Math.random() * ((-120) - (-600) + 1) + (-600));
  for(let i = 0; i < 2; i++) {
      pipes[i] = new Pipe(game.width + 200, randomY + (i*(700 + 100)));
  }
  return pipes;
}

let pairPipers = createPairOfPipes();
console.log(pairPipers);

function animation(){
  requestAnimationFrame(animation);
  root.fillStyle = "#296389";
  root.fillRect(0,0,game.width,game.height);
  pairPipers.forEach((pipe)=>{
    pipe.update();
  });
  bird.update();
}

animation();

document.addEventListener('keydown',({keyCode}) => {
  
  switch (keyCode) {
    case 32:
      console.log("space");
      keys.space.pressed = true;
      break;
  }
});

